@import '@/sass/index.scss';































































.main {
    display: flex;
    flex-direction: column;
    
}
.order-container {
    padding: 10px;
    display: flex;
    flex-direction:column;
    padding-right:30px;
    width: 300px;
    height: 100%;
    background-color: #F5F4F0;
    border-color: black;
    overflow: scroll;
    text-align: left;
    border-color: black;
    border-style: solid;
    border-width: 0.5px;
}

.text-order{
    margin-top: 10px;
}
.empty {
    font-size: 14px;
}


.item-summary {
display: flex;
flex-direction: row;
padding-bottom: 30px;
padding-left: 20px;
justify-content: center;
}
.product-image{
    display: flex;
    flex-direction: row;
    padding-right: 20px;
}
.product-information{
    padding-left: 30px;
    direction: flex;
    flex-direction: row;
}
.each-items {
    display: flex;
    flex-direction: row;
}
button {
    width: 370px;
    height: 47px;
    margin-top: 10px;
    margin-bottom: 30px;
    background-color: black;
    color: white;
    cursor: pointer;
  }
  .total-order{
      padding-bottom: 10px;
  }
