@import '@/sass/index.scss';








































































































































































.delivery-details {
    margin: $space/4 $space;
}

.order-form{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    text-align: left;

    .zip-city {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .zip, 
    .city {
        display: flex;
        flex-direction: column;

        input {
            flex: 1;
            padding: 4% 5%;
        }
    }

    .zip {
        margin-right: 5%;
    }
}

label{
    line-height: 1em;
}
.input-field {
    font-size: 14px;
	font-family: 'Raleway', sans-serif;
    width: 100%;
    padding: 4% 5%;
    margin-bottom: 20px;
}

.total  {
    margin: auto;
}

div label.required:after
{
    color: red;
    content: " *";
}

.item-summary {
    display: flex;
    flex-direction: row;
    padding-bottom: 30px;
    padding-left: 20px;
    justify-content: center;
}
.product-information{
    padding-left: 30px;;
}

